
<div class="py-4">
  <vuetable-filter-bar />
  <div class="vuetable-pagination clearafter">
    <vuetable-pagination-info
      ref="paginationInfoTop"
      info-template="Displaying {from} to {to} of {total} data samples"
    />
    <vuetable-pagination
      ref="paginationTop"
      @vuetable-pagination:change-page="onChangePage"
    />
  </div>
  <div class="overflow-x-auto overflow-y-hidden">
    <vuetable
      ref="vuetable"
      :api-url="apiUrl"
      :append-params="moreParams"
      :css="css"
      :fields="fields"
      :per-page="20"
      :sort-order="sortOrder"
      @vuetable:pagination-data="onPaginationData"
      @vuetable:row-clicked="onRowClicked"
      @vuetable:loaded="onLoaded"
    >
      <template
        slot="sample-date"
        slot-scope="props"
      >
        <data-sample-date
          :date="props.rowData.dateCreated"
          :query="props.rowData.query"
          :url="props.rowData.url"
        />
      </template>
      <template
        slot="sample-device"
        slot-scope="props"
      >
        <data-sample-device
          :device="props.rowData.device"
          :mobile="props.rowData.mobile"
        />
      </template>
      <template
        slot="load-time-bar"
        slot-scope="props"
      >
        <request-bar-chart :row-data="props.rowData" />
      </template>
    </vuetable>
  </div>
  <div class="vuetable-pagination clearafter">
    <vuetable-pagination-info
      ref="paginationInfo"
      info-template="Displaying {from} to {to} of {total} data samples"
    />
    <vuetable-pagination
      ref="pagination"
      @vuetable-pagination:change-page="onChangePage"
    />
  </div>
</div>
